import React from 'react';
import { connect } from 'react-redux';
import { Router } from 'router5';
import { RouterProvider } from 'react-router5';
import { RouteNode } from '@/services/router/components';
import { RootComponents } from '@/_components/root-components';
import { getIsAppError } from '@/services/redux/root-modules/ui-module';
import { Text } from '@/_components/text';
import '@/styles/global.css';
import '@/styles/variables.module.scss';

type PropTypes = {
  router: Router;
  isAppNetError: boolean;
};

export const WrappedViewComponent = ({ router, isAppNetError }: PropTypes) => (
  <>
    <RootComponents />
    <RouterProvider key={1} router={router}>
      {isAppNetError ? (
        <Text
          text=" ,    "
          size="h1"
          color="Black"
        />
      ) : (
        <RouteNode nodeName="">{({ content }) => content}</RouteNode>
      )}
    </RouterProvider>
  </>
);

const mapStateToProps = state => ({ isAppNetError: getIsAppError(state) });

export const App = connect(mapStateToProps)(WrappedViewComponent);
